import './Disclaimer.css'
import { useState } from 'react'
import { LayerGroup } from 'react-leaflet'

export function Disclaimer() {
  const [open, setOpen] = useState(false)

  const toggleDisclaimer = () =>{
      setOpen(!open)
      console.log(open)
  }

  const title = () => {
    return(<span style={{color:'blue'}}>Disclaimer</span>)
  }

  const text = () => {
    return(<span style={{color:'black'}}>This map is intended for demonstration and is not an accurate representation.</span>)
  }

  return (
    <LayerGroup eventHandlers={{
        click: ()=>{toggleDisclaimer()}
    }}>
      <button onClick={toggleDisclaimer} className="leaflet-control-attribution leaflet-control leaflet-disclaimer">
        {open ? text() : title()}
      </button>
    </LayerGroup>
  )
}
