import {Grid, Typography} from '@mui/material';
import {HomeRepairService, LaptopMac } from '@mui/icons-material';

export function TechnicalSkills(){
    return <>
            <Grid>
                <Grid item>
                    <Typography variant="subtitle2" noWrap>
                        <HomeRepairService className='profile-icon'/>
                        <u style={{color:"darkblue"}}>Automation Skills</u>:
                    </Typography>
                    <Typography component={'span'} variant="body2" align="justify">
                        <span style={{display:"flex",flexDirection:"column", justifyContent:"space-between"}}>
                        <ul style={{lineHeight:"1.2", listStyleType:"square", paddingLeft:'20px'}}>
                                <li>
                                    <span>Experienced with <strong>Siemens</strong>, <strong>Rockwell</strong>, and <strong>Schneider Electric</strong> Automation Platforms</span>
                                </li>
                                <li>
                                    <span>Knowledge of <strong>Programmable Logic Controllers (PLC)</strong>, <strong>SCADA</strong>, <strong>HMI</strong>, <strong>Electric Drives (VFD)</strong> and <strong>Field Instrumentation</strong></span>
                                </li>
                                <li>
                                    <span>Knowledge of <strong>Modbus</strong>, <strong>Profinet</strong>, <strong>Ethernet/IP</strong>, <strong>DNP3</strong>, <strong>IEC61850</strong>, <strong>OPC UA</strong> communication protocols</span>
                                </li>
                            </ul>
                        </span>
                    </Typography>
                    <Typography variant="subtitle2" noWrap>
                        <LaptopMac className='profile-icon'/>
                        <u style={{color:"darkblue"}}>Programming Skills</u>:
                    </Typography>
                    <Typography component={'span'} variant="body2" align="justify">
                        <span style={{display:"flex", justifyContent:"space-between"}}>
                            <ul style={{listStyleType:"square", paddingLeft:'20px'}}>
                                <table style={{margin:"0px", padding:"0px", lineHeight:"0.9"}}>
                                <tbody>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Languages</span></li></td>
                                        <td><em>Python, Javascript</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Environment</span></li></td>
                                        <td><em>Windows, Linux, Node.js</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Web</span></li></td>
                                        <td><em>HTML, CSS, Javascript</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Mobile</span></li></td>
                                        <td><em>Flutter (Cross-platform)</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Communication</span></li></td>
                                        <td><em>REST, MQTT, SocketIO</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Databases</span></li></td>
                                        <td><em>MS-SQL, MySQL, MongoDB</em></td>
                                    </tr>
                                    <tr>
                                        <td style={{paddingRight:"10px"}}><li><span>Framework</span></li></td>
                                        <td><em>React, Material UI</em></td>
                                    </tr>
                                    </tbody>
                                </table>
                            </ul>
                        </span>
                    </Typography>
                </Grid>
            </Grid>
        </>
    }