import React from 'react'
import { AntLine } from '../MapUtils/AntPath/AntPath'
import { SubTransmissionJGPath } from './SubTransmissionJGData'

const options = {
    color:"OrangeRed",
    pulseColor:"white",
    delay:600,
    paused:false,
    reversed:false,
    dashArray:[5,10],
    hardwareAccelerated:true,
    smoothFactor: 1.0
}

export const SubTransmissionJG = () => {
  return (
    <AntLine positions={SubTransmissionJGPath} options={options} />
  )
}
