import {useState} from 'react';
import { useMapEvents } from 'react-leaflet';

import React from 'react'

export function ZoomLevel(props) {
    const [currentZoom, setCurrentZoom] = useState(props.initialZoom ? props.initialZoom : 12);
    const map = useMapEvents({
    zoomend: () => {
      setCurrentZoom(map.getZoom());
    },
  });
    return <div className="leaflet-control-attribution leaflet-control">
        Zoom Level : {currentZoom}
    </div>
  }