import './Introduction.css';
import KaushalPortrait from 'Assets/Images/Kaushal/KaushalPortrait.png';
import { Button } from '@mui/material';

function Introduction() {
    return <div className='app'>
    <div className="intro">
    <img src={KaushalPortrait} alt="Kaushal Kumar Joshi" />
    <div className='text'>
      <h1>Hi, I'm Kaushal</h1>
      <p>I'm Industrial Automation Engineer by profession with experience in Mobile Application and Full Stack Web Development.</p>
      <div>
        <Button variant="contained" size="medium" href="/#/profile" sx={{margin:"5px 5px"}}>View Profile</Button>
        <Button variant="contained" size="medium" href="/#/portfolio" sx={{margin:"5px 5px"}}>View Portfolio</Button>
      </div>
    </div>
  </div>
  </div>
}

export default Introduction;