import './Profile.css';
import React from 'react';
import { Button, Container, CssBaseline, Grid, Paper } from '@mui/material';
import { useTheme } from '@emotion/react';
import { ThemeProvider } from '@emotion/react';
import { Header } from './components/Header';
import { Summary } from './components/Summary';
import { Employment } from './components/Employment';
import { Education } from './components/Education';
import { ProfilePicture } from './components/ProfilePicture';
import { TechnicalSkills } from './components/TechnicalSkills';
import { PersonalSkills } from './components/PersonalSkills';

export function Profile() {
    const theme = useTheme();
    return <>
            <CssBaseline enableColorScheme/>
            <ThemeProvider theme={theme}>
                <div style={{display:"flex"}}>
                    <Container fixed padding="10px">
                        <Paper elevation={6} style={{padding:'50px 50px 50px 50px', margin:'10px',}}>
                            <Header />
                            <Container fixed style={{padding:"25px"}}>
                                <Grid container direction="row" spacing={2}>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="summary">
                                        <Summary />
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="profile-picture">
                                        <ProfilePicture/>
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="employment">
                                        <Employment />
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="technical-skills">
                                        <TechnicalSkills />
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="education">
                                        <Education />
                                    </Grid>
                                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} className="personal-skills">
                                        <PersonalSkills />
                                    </Grid>
                                </Grid>
                            </Container>
                            <div style={{display:"flex", alignItems:"center", justifyContent:"center"}}>
                                <Button variant="contained" size="medium" href="/">Back</Button>
                            </div>
                        </Paper>
                    </Container>
                </div>
            </ThemeProvider>
        </>
    }