import { useState, useRef, useLayoutEffect } from 'react'
import { styled, useTheme } from '@mui/material/styles'
import KaushalPortrait from 'Assets/Images/Kaushal/KaushalPortrait.png'
import MuiAppBar from '@mui/material/AppBar'
import MuiDrawer from '@mui/material/Drawer'
import {
  Avatar,
  Box,
  Container,
  Divider,
  IconButton,
  List,
  Toolbar,
  Typography,
} from '@mui/material'
import {
  ChevronLeft,
  ChevronRight,
  Dashboard,
  Insights,
  Map,
  Menu,
  SupervisorAccount,
} from '@mui/icons-material'
import { Outlet } from 'react-router-dom'
import { CustomListItem } from './Components/NavigationUtils/CustomListItem'

const drawerWidth = 240

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: 'hidden',
})

const closedMixin = (theme) => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(8)} + 1px)`,
})

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  backgroundColor: 'whitesmoke',
  color: 'darkblue',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}))

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open' || 'pwidth',
})(({ theme, open, pwidth }) => ({
  left: { pwidth },
  width: `calc(100% - ${pwidth}px)`,
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  flexDirection: 'row',
  zIndex: theme.zIndex.drawer + 1,
  backgroundColor: 'whitesmoke',
  color: 'darkblue',
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}))

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  backgroundColor: 'whitesmoke',
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: 'nowrap',
  boxSizing: 'border-box',
  ...(open && {
    ...openedMixin(theme),
    '& .MuiDrawer-paper': openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    '& .MuiDrawer-paper': closedMixin(theme),
  }),
}))

export const Navigation = () => {
  const theme = useTheme()
  const [open, setOpen] = useState(false)
  const [width, setWidth] = useState(0)
  const drawer = useRef()

  useLayoutEffect(() => {
    setWidth(drawer.current.offsetWidth)
  }, [])

  // const handleDrawerOpen = () => {
  //   setOpen(true);
  // };

  // const handleDrawerClose = () => {
  //   setOpen(false);
  // };

  const handleDrawerToggle = () => {
    setOpen(!open)
  }

  return (
    <>
      <AppBar elevation={1} position="fixed" open={open} pwidth={width}>
        <Toolbar>
          <IconButton
            disableRipple
            size="small"
            onClick={handleDrawerToggle}
            sx={{
              position: 'relative',
              left: '-2.35em',
              bottom: '-1.75em',
              borderRadius: '50%',
              color: '#f5f5f5',
              background: 'darkblue',
              opacity: '75%',
              [theme.breakpoints.only('xs')]: {
                left: '-1.85em',
                bottom: '-1.55em',
              },
            }}
          >
            {open ? <ChevronLeft /> : <ChevronRight />}
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            My Portfolio
          </Typography>
        </Toolbar>
        <Toolbar>
          <Avatar alt="Kaushal Joshi" src={KaushalPortrait} />
        </Toolbar>
      </AppBar>
      <Drawer
        PaperProps={{
          elevation: 2,
          sx: { backgroundColor: 'whitesmoke', color: 'darkblue' },
        }}
        variant="permanent"
        open={open}
        ref={drawer}
      >
        <DrawerHeader>
          <Menu />
        </DrawerHeader>
        <Divider />
        <List>
          <CustomListItem
            text={'Dashboards'}
            url={'#/portfolio/dashboards'}
            icon={Dashboard}
            drawer={open}
          />
          <CustomListItem
            text={'User Administration'}
            url={'#/portfolio/admin'}
            icon={SupervisorAccount}
            drawer={open}
          />
          <CustomListItem
            text={'Geospatial'}
            url={'#/portfolio/gis'}
            icon={Map}
            drawer={open}
          />
          <CustomListItem
            text={'Charts'}
            url={'#/portfolio/charts'}
            icon={Insights}
            drawer={open}
          />
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          display: 'flex',
          flexDirection: 'column',
          height: '100vh',
          maxHeight: '100vh',
        }}
      >
        <Container
          variant="fluid"
          sx={{
            flexGrow: 0,
            ...theme.mixins.toolbar,
          }}
        />
        <Outlet />
      </Box>
    </>
  )
}
