const Capitalize = (string) => {
    return string[0].toUpperCase() + string.substring(1);
}

export const is = (type, obj) => {
    var isOfType = Object.prototype.toString.call(obj).slice(8, -1);
    return obj !== undefined && obj !== null && isOfType === type;
}

export const keysToSkip = ['location','status','error','coords', 'cluster']

export const generateContent = (data) => {
    let generatedContent = []
    for (let [key, value] of Object.entries(data)){
    if(!keysToSkip.includes(key)){
        if (is('Object',value)) {
        let keystring = []
        for(let [k, v] of Object.entries(value)){
        keystring.push(`${v} ${k}`)
        }
        generatedContent.push(`${Capitalize(key)} : ${keystring.join(', ')}`)
      } else {
        generatedContent.push(`${Capitalize(key)} : ${value}`)
      }
     }
    }
    return generatedContent
}