import { Container, Grid, Typography } from '@mui/material';
import { Person, LocationOn, Mail } from '@mui/icons-material';


export function Header(){
    return <>
            <Grid container spacing={2} alignItems="flex-end">
                <Grid item xs={12} sm={12} md={6} alignItems="center">
                    <Typography variant="h6">
                        <Person className='profile-icon'/>
                        KAUSHAL KUMAR JOSHI
                    </Typography>
                    <Typography variant="body2" justify='center'>
                        <LocationOn className='profile-icon'/>
                        NOIDA (U.P.), India
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={12} md={6} style={{paddingTop:"0px"}}>
                    <Typography variant="body2" className="email">
                        <Mail className='profile-icon'/>
                        <a style={{textDecoration:"none", color:"black"}} href="mailto:joshikaushal5@gmail.com">joshikaushal5@gmail.com</a>
                    </Typography>
                </Grid>
            </Grid>
            <Container  style={{width:"100%", height:"5px", backgroundColor:"darkblue", margin:"5px"}} />    
        </>
    }

