import './MarkerCluster.css'
import L from 'leaflet'
import { Marker, useMap, Popup, Tooltip } from 'react-leaflet'
import { useCallback, useEffect, useState } from 'react'
import useSupercluster from 'use-supercluster'
import { Card, Typography } from '@mui/material'
import {generateContent} from '../KeyChecker/KeyChecker'

const fetchIcon = (count, size, assetName) => {
  //console.log(count, size, assetName);
  return L.divIcon({
    html: `<div class="cluster-marker-${assetName}" style="height:${size}px;width:${size}px">
                ${count}
            </div>`,
  })
}

export const MarkerCluster = ({
  assetName,
  assets,
  iconsMap,
  image,
  imageAlt,
  initialZoom,
  radius
}) => {
  const maxZoom = 22
  const [bounds, setBounds] = useState(null)
  const [zoom, setZoom] = useState(initialZoom)
  const map = useMap()

  function updateMap() {
    const mapbounds = map.getBounds()
    setBounds([
      mapbounds.getSouthWest().lng,
      mapbounds.getSouthWest().lat,
      mapbounds.getNorthEast().lng,
      mapbounds.getNorthEast().lat,
    ])
    setZoom(map.getZoom())
  }

  const onMove = useCallback(() => {
    updateMap()
    // eslint-disable-next-line
  }, [map])

  useEffect(() => {
    onMove()
    map.on('move', onMove)
    return () => {
      map.off('move', onMove)
    }
  }, [map, onMove])

  const points = assets.map(function (asset) {
    //console.log(asset);
    const point = {
      type: 'Feature',
      properties: {
        cluster: false,
        assetId: asset.location,
        category: asset.location.split(' ')[1],
      },
      geometry: {
        type: 'Point',
        coordinates: [asset.coords.lon, asset.coords.lat],
      },
    }
    Object.assign(point.properties, asset)
    // console.log(point);
    return point
  })

  const { clusters, supercluster } = useSupercluster({
    points: points,
    bounds: bounds,
    zoom: zoom,
    options: {
      radius: radius,
      maxZoom: 17,
    },
  })

  return (
    <>
      {clusters.map((cluster) => {
        const [longitude, latitude] = cluster.geometry.coordinates
        const { cluster: isCluster, point_count: pointCount } =
          cluster.properties
        //console.log(cluster);
        if (isCluster) {
          return (
            <Marker
              key={cluster.id}
              position={[latitude, longitude]}
              icon={fetchIcon(
                pointCount,
                10 + (pointCount / points.length) * 40,
                assetName
              )}
              eventHandlers={{
                click: () => {
                  const expansionZoom = Math.min(
                    supercluster.getClusterExpansionZoom(cluster.id),
                    maxZoom
                  )
                  map.setView([latitude, longitude], expansionZoom, {
                    animate: true,
                  })
                },
              }}
            />
          )
        }

        return (
          <Marker
            key={cluster.properties.location}
            icon={iconsMap[cluster.properties.status]}
            position={[
              cluster.properties.coords.lat,
              cluster.properties.coords.lon,
            ]}
          >
            <Popup closeButton={false} closeOnClick={true}>
              <Card elevation={0} sx={{ margin: '0px' }}>
                <Typography variant="h6">
                  {cluster.properties.location}
                </Typography>
                <img alt={imageAlt} src={image} height="70px" />
                <Typography component="span" variant="body2">
                  <ul
                    style={{
                      listStyle: 'none',
                      margin: '0',
                      padding: '0',
                      lineHeight: '1.2',
                    }}
                  >
                    {generateContent(cluster.properties).map((content) => {
                      return <li key={content}>{content}</li>
                    })}
                    <li
                      style={{
                        color: !cluster.properties.error
                          ? 'green'
                          : cluster.properties.status !== 'overflow'
                          ? 'red'
                          : '#d9b200',
                      }}
                    >
                      Status : {cluster.properties.status}
                    </li>
                  </ul>
                </Typography>
              </Card>
            </Popup>
            <Tooltip>{cluster.properties.location}</Tooltip>
          </Marker>
        )
      })}
    </>
  )
}
