import React from 'react'
import { AntLine } from '../MapUtils/AntPath/AntPath'
import { MainTransmissionPath } from './MainTransmissionData'

const options = {
    color:"green",
    pulseColor:"white",
    delay:400,
    paused:false,
    reversed:false,
    dashArray:[20,20],
    hardwareAccelerated:true,
    smoothFactor: 1.0
}

export const MainTransmission = () => {
  return (
    <AntLine positions={MainTransmissionPath} options={options} />
  )
}
