import React from 'react'
import { Card, Typography } from '@mui/material'
import PumpingStationImage from 'Assets/Images/Water/PumpingStation.png'
import {
  LayerGroup,
  LayersControl,
  Marker,
  Popup,
  Tooltip,
} from 'react-leaflet'
import { MarkerCluster } from '../MapUtils/MarkerCluster/MarkerCluster'
import { LayerData, PumpingStationsData } from './PumpingStationsData'
import {
  PumpingStationGreenIcon,
  PumpingStationGreyIcon,
  PumpingStationRedIcon,
  PumpingStationYellowIcon,
} from '../Icons/Icons'
import { generateContent } from '../MapUtils/KeyChecker/KeyChecker'

const PumpingStationIconsMap = {
  running: PumpingStationGreenIcon,
  faulted: PumpingStationRedIcon,
  stopped: PumpingStationYellowIcon,
  offline: PumpingStationGreyIcon,
}

const GenerateMarker = () => {
  return (
    <>
      {PumpingStationsData.map((PumpingStation) => {
        return (
          <Marker
            key={PumpingStation.location}
            icon={PumpingStationIconsMap[PumpingStation.status]}
            position={[PumpingStation.coords.lat, PumpingStation.coords.lon]}
          >
            <Popup closeButton={false} closeOnClick={true}>
              <Card elevation={0} sx={{ margin: '0px' }}>
                <Typography variant="h6">{PumpingStation.location}</Typography>
                <img
                  alt="Pumping Station"
                  src={PumpingStationImage}
                  height="70px"
                />
                <Typography component="span" variant="body2">
                  <ul
                    style={{
                      listStyle: 'none',
                      margin: '0',
                      padding: '0',
                      lineHeight: '1.2',
                    }}
                  >
                    {generateContent(PumpingStation).map((content) => {
                      return <li key={content}>{content}</li>
                    })}
                    <li
                      style={{
                        color: !PumpingStation.error
                          ? 'green'
                          : PumpingStation.status !== 'overflow'
                          ? 'red'
                          : '#d9b200',
                      }}
                    >
                      Status : {PumpingStation.status}
                    </li>
                  </ul>
                </Typography>
              </Card>
            </Popup>
            <Tooltip>{PumpingStation.location}</Tooltip>
          </Marker>
        )
      })}
    </>
  )
}

export const PumpingStations = ({ initialZoom, clustering = false }) => {
  return (
    <>
      <LayersControl.Overlay checked name={LayerData.name}>
        <LayerGroup>
          {clustering ? (
            <MarkerCluster
              assetName="ps"
              assets={PumpingStationsData}
              iconsMap={PumpingStationIconsMap}
              image={PumpingStationImage}
              imageAlt="Pumping Stations"
              initialZoom={initialZoom}
              radius={200}
            />
          ) : (
            <GenerateMarker />
          )}
        </LayerGroup>
      </LayersControl.Overlay>
    </>
  )
}
