import React from 'react'
import { Card, Typography } from '@mui/material'
import OverheadReservoirImage from 'Assets/Images/Water/OverheadReservoir.png'
import {
  LayerGroup,
  LayersControl,
  Marker,
  Popup,
  Tooltip,
} from 'react-leaflet'
import { MarkerCluster } from '../MapUtils/MarkerCluster/MarkerCluster'
import { LayerData, OverheadReservoirsData } from './OverheadReservoirsData'
import {
  OverheadReservoirGreenIcon,
  OverheadReservoirGreyIcon,
  OverheadReservoirRedIcon,
  OverheadReservoirYellowIcon,
} from '../Icons/Icons'
import { generateContent } from '../MapUtils/KeyChecker/KeyChecker'

const OverheadReservoirIconsMap = {
  filled: OverheadReservoirGreenIcon,
  empty: OverheadReservoirRedIcon,
  overflow: OverheadReservoirYellowIcon,
  offline: OverheadReservoirGreyIcon,
}

const GenerateMarker = () => {
  return (
    <>
      {OverheadReservoirsData.map((OverheadReservoir) => {
        return (
          <Marker
            key={OverheadReservoir.location}
            icon={OverheadReservoirIconsMap[OverheadReservoir.status]}
            position={[
              OverheadReservoir.coords.lat,
              OverheadReservoir.coords.lon,
            ]}
          >
            <Popup closeButton={false} closeOnClick={true}>
              <Card elevation={0} sx={{ margin: '0px' }}>
                <Typography variant="h6">
                  {OverheadReservoir.location}
                </Typography>
                <img
                  alt="Overhead Reservoir"
                  src={OverheadReservoirImage}
                  height="70px"
                />
                <Typography component="span" variant="body2">
                  <ul
                    style={{
                      listStyle: 'none',
                      margin: '0',
                      padding: '0',
                      lineHeight: '1.2',
                    }}
                  >
                    {generateContent(OverheadReservoir).map((content) => {
                      return <li key={content}>{content}</li>
                    })}

                    <li
                      style={{
                        color: !OverheadReservoir.error
                          ? 'green'
                          : OverheadReservoir.status !== 'overflow'
                          ? 'red'
                          : '#d9b200',
                      }}
                    >
                      Status : {OverheadReservoir.status}
                    </li>
                  </ul>
                </Typography>
              </Card>
            </Popup>
            <Tooltip>{OverheadReservoir.location}</Tooltip>
          </Marker>
        )
      })}
    </>
  )
}

export const OverheadReservoirs = ({ initialZoom, clustering = false }) => {
  return (
    <>
      <LayersControl.Overlay checked name={LayerData.name}>
        <LayerGroup>
          {clustering ? (
            <MarkerCluster
              assetName="ohsr"
              assets={OverheadReservoirsData}
              iconsMap={OverheadReservoirIconsMap}
              image={OverheadReservoirImage}
              imageAlt="Overhead Reservoir"
              initialZoom={initialZoom}
              radius={110}
            />
          ) : (
            <GenerateMarker />
          )}
        </LayerGroup>
      </LayersControl.Overlay>
    </>
  )
}
