import Picture from 'Assets/Images/Kaushal/ResumePortrait.png';
import {Grid} from '@mui/material';

export function ProfilePicture(){
    return <>
            <Grid>
                <Grid item>
                    <div style={{display:"flex", alignItems:"center", justifyContent:"flex-end"}}>
                        <img style={{justifySelf:"flex-end"}}src={Picture} alt="Kaushal Kumar Joshi"></img>
                    </div>
                </Grid>
            </Grid>
        </>
    }