import React from 'react'
import { Card, Typography } from '@mui/material'
import MasterReservoirImage from 'Assets/Images/Water/MasterReservoir.png'
import {
  LayerGroup,
  LayersControl,
  Marker,
  Popup,
  Tooltip,
} from 'react-leaflet'
import { MarkerCluster } from '../MapUtils/MarkerCluster/MarkerCluster'
import { LayerData, MasterReservoirsData } from './MasterReservoirsData'
import {
  MasterReservoirGreenIcon,
  MasterReservoirGreyIcon,
  MasterReservoirRedIcon,
  MasterReservoirYellowIcon,
} from '../Icons/Icons'
import {generateContent} from '../MapUtils/KeyChecker/KeyChecker'

const MasterReservoirIconsMap = {
  filled: MasterReservoirGreenIcon,
  empty: MasterReservoirRedIcon,
  overflow: MasterReservoirYellowIcon,
  offline: MasterReservoirGreyIcon,
}

const GenerateMarker = () => {
    return (
      <>
        {MasterReservoirsData.map((MasterReservoir) => {
            return (
              <Marker
                key={MasterReservoir.location}
                icon={MasterReservoirIconsMap[MasterReservoir.status]}
                position={[
                  MasterReservoir.coords.lat,
                  MasterReservoir.coords.lon,
                ]}
              >
                <Popup closeButton={false} closeOnClick={true}>
                  <Card elevation={0} sx={{ margin: '0px' }}>
                    <Typography variant="h6">
                      {MasterReservoir.location}
                    </Typography>
                    <img
                      alt="Master Reservoir"
                      src={MasterReservoirImage}
                      height="70px"
                    />
                    <Typography component="span" variant="body2">
                      <ul
                        style={{
                          listStyle: 'none',
                          margin: '0',
                          padding: '0',
                          lineHeight: '1.2',
                        }}
                      >
                        {generateContent(MasterReservoir).map((content) => {
                      return <li key={content}>{content}</li>
                    })}
                        <li
                          style={{
                            color: !MasterReservoir.error
                              ? 'green'
                              : MasterReservoir.status !== 'overflow'
                              ? 'red'
                              : '#d9b200',
                          }}
                        >
                          Status : {MasterReservoir.status}
                        </li>
                      </ul>
                    </Typography>
                  </Card>
                </Popup>
                <Tooltip>{MasterReservoir.location}</Tooltip>
              </Marker>
            )
          })}
      </>
    )
}

export const MasterReservoirs = ({ initialZoom, clustering = false }) => {
  return (
    <>
      <LayersControl.Overlay checked name={LayerData.name}>
        <LayerGroup>
        {clustering ? (
            <MarkerCluster
              assetName="mr"
              assets={MasterReservoirsData}
              iconsMap={MasterReservoirIconsMap}
              image={MasterReservoirImage}
              imageAlt="Master Reservoir"
              initialZoom={initialZoom}
              radius={110}
            />
          ) : (
            <GenerateMarker />
          )}
        </LayerGroup>
      </LayersControl.Overlay>
    </>
  )
}
