import React from 'react'
import { AntLine } from '../MapUtils/AntPath/AntPath'
import { SubTransmissionWPath } from './SubTransmissionWData'

const options = {
    color:"Blue",
    pulseColor:"white",
    delay:600,
    paused:true,
    reversed:false,
    dashArray:[5,10],
    hardwareAccelerated:true,
    smoothFactor: 1.0
}

export const SubTransmissionW = () => {
  return (
    <AntLine positions={SubTransmissionWPath} options={options} />
  )
}
