import './App.css';
import React from 'react';
import {HashRouter as Router, Routes, Route} from 'react-router-dom';
import { ThemeProvider } from '@emotion/react';
import { createTheme, responsiveFontSizes } from '@mui/material/styles';
import { Home } from './Home/Home';
import { Profile } from './Profile/Profile';
import { Portfolio } from './Portfolio/Portfolio';
import { Dashboards } from './Portfolio/Navigation/Components/Dashboards/Dashboards';
import { UserAdministration } from './Portfolio/Navigation/Components/UserAdministration/UserAdministration';
import { Geospatial } from './Portfolio/Navigation/Components/Geospatial/Geospatial';
import { Charts } from './Portfolio/Navigation/Components/Charts/Charts';

let theme = createTheme();
theme=responsiveFontSizes(theme);

export function App() {
  return (
    <ThemeProvider theme={theme}>
    <Router>
      <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/profile" element={<Profile />}/>          
          <Route path="/portfolio" element={<Portfolio />} >
            <Route path="dashboards" element={<Dashboards />} />
            <Route path="admin" element={<UserAdministration />} />
            <Route path="gis" element={<Geospatial />} />
            <Route path="charts" element={<Charts />} />
          </Route>
          <Route path="*" element={<h1>Not Found</h1>} />
      </Routes>
    </Router>
    </ThemeProvider>
  );
}