import React from 'react'
import { ListItem, ListItemButton, ListItemIcon, ListItemText} from '@mui/material';

export const CustomListItem = (props) => {
  return (
    <ListItem key={props.text} disablePadding sx={{ display: 'block' }}>
                <ListItemButton
                  href={props.url}
                  sx={{
                    minHeight: 48,
                    justifyContent: props.drawer ? 'initial' : 'center',
                    px: 2.5,
                  }}>
                <ListItemIcon
                  sx={{
                    color:'darkblue',
                    minWidth: 0,
                    mr: props.drawer ? 3 : 'auto',
                    justifyContent: 'center',
                  }}>
                  <props.icon />
                </ListItemIcon>
                <ListItemText primary={props.text} sx={{ opacity: props.drawer ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
  )
}