import React from 'react'
import { Card, Typography } from '@mui/material'
import AreaReservoirImage from 'Assets/Images/Water/AreaReservoir.png'
import {
  LayerGroup,
  LayersControl,
  Marker,
  Popup,
  Tooltip,
} from 'react-leaflet'
import { MarkerCluster } from '../MapUtils/MarkerCluster/MarkerCluster'
import { AreaReservoirsData, LayerData } from './AreaReservoirsData'
import {
  AreaReservoirGreenIcon,
  AreaReservoirGreyIcon,
  AreaReservoirRedIcon,
  AreaReservoirYellowIcon,
} from '../Icons/Icons'
import {generateContent} from '../MapUtils/KeyChecker/KeyChecker'

const AreaReservoirIconsMap = {
  filled: AreaReservoirGreenIcon,
  empty: AreaReservoirRedIcon,
  overflow: AreaReservoirYellowIcon,
  offline: AreaReservoirGreyIcon,
}

const GenerateMarker = () => {
    return (
        <>
            {AreaReservoirsData.map((AreaReservoir) => {
            return (
              <Marker
                key={AreaReservoir.location}
                icon={AreaReservoirIconsMap[AreaReservoir.status]}
                position={[AreaReservoir.coords.lat, AreaReservoir.coords.lon]}
              >
                <Popup closeButton={false} closeOnClick={true}>
                  <Card elevation={0} sx={{ margin: '0px' }}>
                    <Typography variant="h6">
                      {AreaReservoir.location}
                    </Typography>
                    <img
                      alt="Area Reservoir"
                      src={AreaReservoirImage}
                      height="70px"
                    />
                    <Typography component="span" variant="body2">
                      <ul
                        style={{
                          listStyle: 'none',
                          margin: '0',
                          padding: '0',
                          lineHeight: '1.2',
                        }}
                      >
                        {generateContent(AreaReservoir).map((content) => {
                      return <li key={content}>{content}</li>
                    })}
                        <li
                          style={{
                            color: !AreaReservoir.error
                              ? 'green'
                              : AreaReservoir.status !== 'overflow'
                              ? 'red'
                              : '#d9b200',
                          }}
                        >
                          Level : {AreaReservoir.status}
                        </li>
                      </ul>
                    </Typography>
                  </Card>
                </Popup>
                <Tooltip>{AreaReservoir.location}</Tooltip>
              </Marker>
            )
          })}
        </>
    )
}

export const AreaReservoirs = ({ initialZoom, clustering = false }) => {
  return (
    <>
      <LayersControl.Overlay checked name={LayerData.name}>
        <LayerGroup>
        {clustering ? (
            <MarkerCluster
              assetName="ar"
              assets={AreaReservoirsData}
              iconsMap={AreaReservoirIconsMap}
              image={AreaReservoirImage}
              imageAlt="Area Reservoir"
              initialZoom={initialZoom}
              radius={110}
            />
          ) : (
            <GenerateMarker />
          )}
        </LayerGroup>
      </LayersControl.Overlay>
    </>
  )
}
