import './MapLayers.css';
import { LayersControl, TileLayer } from "react-leaflet";
import { MapLayersData } from "./MapLayersData";

const { BaseLayer } = LayersControl;

export const MapLayers = () => {
  return (
    <>
        {MapLayersData.map((MapLayer) => {
            return (
                <BaseLayer 
                key={MapLayer.name} 
                checked={MapLayer.name === "OSM" ? 'checked':''} 
                name={MapLayer.name}
                >
                    <TileLayer
                        className={MapLayer.name === 'OSM Grey' ? 'leaflet-tile-pane-grey' : ''}
                        url={MapLayer.url}
                        attribution={MapLayer.attribution}
                        maxNativeZoom={MapLayer.maxNativeZoom ?? 19}
                        maxZoom={MapLayer.maxZoom ?? 20}
                        subdomains={MapLayer.subdomains ? MapLayer.subdomains : ''}
              />
            </BaseLayer>
            )
        })}
    </>
  )
}
