import './Geospatial.css'
import React from 'react'
import L from 'leaflet'
import { Box } from '@mui/material'
import { LayersControl, MapContainer } from 'react-leaflet'
import { MouseCoordinates } from './MapUtils/MouseCoordinates/MouseCoordinates'
import { ZoomLevel } from './MapUtils/ZoomLevel/ZoomLevel'
import { DefaultIcon } from './Icons/Icons'
import { MapLayers } from './MapLayers/MapLayers'
import { MasterReservoirs } from './MasterReservoirs/MasterReservoirs'
import { AreaReservoirs } from './AreaReservoirs/AreaReservoirs'
import { PumpingStations } from './PumpingStations/PumpingStations';
import { OverheadReservoirs } from './OverheadReservoir/OverheadReservoirs'
import { MainTransmission } from './MainTransmission/MainTransmission'
import { SubTransmissionJG } from './SubTransmissionJG/SubTransmissionJG'
import { SubTransmissionW } from './SubTransmissionW/SubTransmissionW'
import { Disclaimer } from './MapUtils/Disclaimer/Disclaimer'

const InitialZoom = 12

L.Marker.prototype.options.icon = DefaultIcon

const GREATER_NOIDA = [
  [28.341, 77.366],
  [28.585, 77.653],
]

export const Geospatial = () => {
  return (
    <>
      <Box sx={{ flexGrow: '1' }}>
        <MapContainer
          closePopupOnClick={false}
          maxBounds={GREATER_NOIDA}
          sx={{ height: '100%' }}
          center={[28.465673, 77.510948]}
          zoom={InitialZoom}
          // maxZoom={18}
          minZoom={10}
          scrollWheelZoom={true}
        >
          <LayersControl>
            <MapLayers />
            <MasterReservoirs initialZoom={InitialZoom} clustering={true} />
            <AreaReservoirs initialZoom={InitialZoom} clustering={true} />
            <PumpingStations initialZoom={InitialZoom} clustering={true} />
            <OverheadReservoirs initialZoom={InitialZoom} clustering={true} />
            <MainTransmission />
            <SubTransmissionJG/>
            <SubTransmissionW />
          </LayersControl>
          <div className="leaflet-bottom leaflet-left">
            <MouseCoordinates />
          </div>
          <div
            className="leaflet-bottom leaflet-left"
            style={{ marginBottom: '20px' }}
          >
            <ZoomLevel initialZoom={InitialZoom} />
          </div>
          <div
            className="leaflet-bottom leaflet-right"
            style={{ marginBottom: '20px' }}
          >
            <Disclaimer />
          </div>
        </MapContainer>
      </Box>
    </>
  )
}

