import { Grid, Typography } from '@mui/material';
import { Business } from '@mui/icons-material';

export function Employment(){
    return <>
            <Grid>
                <Grid item alignItems="center">
                    <Typography variant="subtitle2" noWrap paddingBottom="10px">
                        <Business className='profile-icon'/>
                        <u style={{color:"darkblue"}}>Employment History</u>:
                    </Typography>
                    <Typography variant="body2" style={{paddingBottom:"10px"}}>
                        <span style={{display:"flex", justifyContent:"space-between"}}>
                            <span>Business Development Professional</span>
                            <span><em>2018 - Present</em></span>
                        </span>
                        <strong><em>Siemens Ltd.</em></strong>
                    </Typography>
                    <Typography variant="body2" style={{paddingBottom:"10px"}}>
                        <span style={{display:"flex", justifyContent:"space-between"}}>
                            <span>Engineer</span>
                            <span><em>2016 - 2018</em></span>
                        </span>
                        <span style={{display:"block"}}>Primary Technical Support</span>
                        <strong><em>Schneider Electric India Pvt. Ltd.</em></strong>
                    </Typography>
                    <Typography variant="body2" style={{paddingBottom:"10px"}}>
                        <span style={{display:"flex", justifyContent:"space-between"}}>
                            <span>Service Engineer</span>
                            <span><em>2016 - 2016</em></span>
                        </span>
                        <strong><em>Deutek Controls Pvt. Ltd.</em></strong>
                        <span style={{display:"block"}}>(Siemens System Integrator)</span>
                    </Typography>
                    <Typography variant="body2" style={{paddingBottom:"10px"}}>
                        <span style={{display:"flex", justifyContent:"space-between"}}>
                            <span>Engineer</span>
                            <span><em>2013 - 2015</em></span>
                        </span>
                        <strong><em>Visiontech Automation</em></strong>
                    </Typography>
                </Grid>
            </Grid>
        </>
    }